#home-icon{
    height: 24px;
    width: 24px;
    fill: #feb;
}

#back-icon{
    height: 24px;
    width: 24px;
    fill: #feb;
}

.star-icon, .star-solid-icon{
    color: #feb;
    height: 18px;
}

.user-icon {
    color: #feb;
    height: 24px;
}

.circle-notch-icon{
    animation: spin 1.5s linear infinite;
    color: #feb;
    height: 36px;
}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}